<template>
  <apexchart v-if="series" height=250 :options="options" :series="series"></apexchart>
</template>

<script>
import VueApexCharts from 'vue-apexcharts'

export default {
  name: 'lkhd-plot',
  props: {
    lkhdPlotDict: Object,
  },
  components: {
    apexchart: VueApexCharts,
  },
  mounted() {
    // this is so that the options is set to optionsOrig every time series changes from null to non-null
    this.options = this.optionsOrig;
  },
  data: function() {
    return {
      series: null,
      options: null,
      //[{name: 'series-1',
      //  data: [{x: 1, y: 30}, {x: 2, y: 40}, {x: 5, y: 20}, {x: 10, y: 50}, {x: 20, y: 30}, {x: 50, y: 10}]  }],
      optionsOrig: {
        chart: {
          id: 'lkhd-plot',
          type: 'line',
        },
        dataLabels: {
          enabled: false,
        },
        title: {
          text: 'Likelihood - allele age',
          align: 'left',
          offsetX: 10
        },
        tooltip: {
          enabled: true,
          shared: true,
          intersect: false,
        },
        xaxis: {
          type: 'numeric',
          title: {text: "time (generations)"},
        },
        yaxis: {
          axisTicks: {show: true,},
          axisBorder: {show: true, color: '#008FFB'},
          labels: {
            formatter: function(val) {return val.toExponential(4);}
          },
          title: {
            text: "Likelihood",
            style: {color: '#008FFB',}
          },
        },
        stroke: {
            width: 1,
        },
      }
    }
  },
  watch: {
    lkhdPlotDict: function(newVal) {
      if ("tmax" in newVal) {
        var o = {...this.options,
          annotations: {
            xaxis: [{
              x: newVal.tmax,
              strokeDashArray: 0,
              borderColor: '#775DD0',
              label: {
                borderColor: '#775DD0',
                style: {
                  color: '#fff',
                  background: '#775DD0',
                },
                text: 'mle='+newVal.tmax.toFixed(0),
                position: 'bottom',
              }
            }]
          },
          colors: ['#4d3a96', '#4576b5'],
        };
        o.chart = {
          id: 'lkhd-plot',
          type: 'area',
        };
        this.options = o;
      } else {
        this.options = this.optionsOrig;
      }
      this.series = newVal.series;
    },
  },
  methods: {
  }
}
</script>