<template>
  <apexchart type="line" height=250 v-if="series" :options="options" :series="series"></apexchart>
</template>

<script>
import VueApexCharts from 'vue-apexcharts'

export default {
  name: 'sample-chart',
  props: {
    samplePlotDict: Array,
  },
  components: {
    apexchart: VueApexCharts,
  },
  data: function() {
    return {
      series: null,
      //[{name: 'series-1',
      //  data: [{x: 1, y: 30}, {x: 2, y: 40}, {x: 5, y: 20}, {x: 10, y: 50}, {x: 20, y: 30}, {x: 50, y: 10}]  }],
      options: {
        chart: {
          id: 'sample-chart',
          type: 'line',
          stacked: true,
        },
        title: {
          text: 'Samples',
          align: 'left',
          offsetX: 10
        },
        tooltip: {
          enabled: true,
          shared: true,
          intersect: false,
        },
        xaxis: {
          type: 'numeric',
          title: {text: "time (generations)"},
        },
        yaxis: [
          {
            axisTicks: {show: true,},
            axisBorder: {show: true, color: '#008FFB'},
            min: 0,
            max: () => {
              const x1 = this.series[0].data.map(x => x[1]);
              const x2 = this.series[1].data.map(x => x[1]);
              return Math.max(...x1.map((n, i) => {return n + x2[i]}))
            },
            labels: {
              formatter: function(val) {return val.toFixed(0);}
            },
            title: {
              text: "Sample counts",
              style: {color: '#008FFB',}
            },
            //tooltip: {enabled: false, shared: true}
          },
          {
            //seriesName: 'non-mutant count',
            show: false,
            labels: {
              formatter: function(val) {return val.toFixed(0);}
            },
          },
          {
            //seriesName: 'mutant proportion',
            min: 0,
            max: 1,
            axisTicks: {show: true,},
            axisBorder: {show: true, color: '#FEB019'},
            labels: {
              //style: {colors: '#FEB019',}
            },
            title: {
              text: "Mutant proportion",
              //style: {color: '#FEB019',}
            },
          },
          {
            //seriesName: 'population size',
            opposite: true,
            axisTicks: {show: true},
            axisBorder: {show: true, color: '#FF4560'},
            min: 0,
            max: (max) => {
              return max * 1.1;
            },
            labels: {
              formatter: function(val) {return val.toFixed(0);}
              //style: {colors: '#FEB019',}
            },
            title: {
              text: "Population size",
              //style: {color: '#FEB019',}
            },
          }
        ],
        dataLabels: {
          enabled: false,
        },
        stroke: {
          width: [1, 1, 2, 4],
          curve: ['smooth', 'smooth', 'smooth', 'stepline'],
        },
        fill: {
          opacity: [0.7, 0.1, 0.5, 1],
        },
      }
    }
  },
  watch: {
    samplePlotDict: function(newVal) {
      this.series = newVal;
    },
  },
  methods: {
    updateChart() {
      const max = 90;
      const min = 20;
      const newData = this.series[0].data.map(() => {
        return Math.floor(Math.random() * (max - min + 1)) + min
      })
      // In the same way, update the series option
      this.series = [{
        data: newData
      }]
    },
  }
}
</script>