import Home from '../views/Home.vue';
//import Doc from '../views/Doc.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  // {
  //   path: '/script/kbe/saa-mle.html',
  //   name: 'Doc',
  //   component: Doc
  // },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
