<template>
  <apexchart v-if="series" height=250 :options="options" :series="series"></apexchart>
</template>

<script>
import VueApexCharts from 'vue-apexcharts'

export default {
  name: 'gss-plot',
  props: {
    gssPlotDict: Object,
  },
  components: {
    apexchart: VueApexCharts,
  },
  mounted() {
    this.options = this.optionsOrig;
  },
  data: function() {
    return {
      series: null,
      options: null,
      //[{name: 'series-1',
      //  data: [{x: 1, y: 30}, {x: 2, y: 40}, {x: 5, y: 20}, {x: 10, y: 50}, {x: 20, y: 30}, {x: 50, y: 10}]  }],
      optionsOrig: {
        chart: {
          id: 'gss-plot',
          type: 'area',
        },
        zoom: {
          enabled: true,
        },
        dataLabels: {
          enabled: false
        },
        title: {
          text: 'Likelihood - selection coefficient',
          align: 'left',
          offsetX: 10
        },
        markers: {
          size: 5,
          hover: {
            size: 9
          }
        },
        tooltip: {
          enabled: true,
        },
        xaxis: {
          type: 'numeric',
          axisTicks: {show: true,},
          labels: {
            formatter: function(val) {if (val) return val.toFixed(3); else return val;}
          },
          title: {text: "selection coefficient s"},
        },
        yaxis: {
          axisTicks: {show: true,},
          axisBorder: {show: true, color: '#008FFB'},
          labels: {
            formatter: function(val) {return val.toExponential(4);}
          },
          title: {
            text: "Likelihood",
            style: {color: '#008FFB',}
          },
        },
        stroke: {
          curve: 'straight',
          width: 2,
        },
      }
    }
  },
  watch: {
    gssPlotDict: function(newVal) {
      var o;
      if ("smax" in newVal) {
        o = {...this.options, annotations: {
          xaxis: [{
            x: newVal.smax,
            strokeDashArray: 0,
            borderColor: '#775DD0',
            label: {
              borderColor: '#775DD0',
              style: {
                color: '#ffffff',
                background: '#775DD0',
              },
              text: 'mle='+newVal.smax.toFixed(4),
              position: 'bottom',
            }
          },
          // {
          //   x: newVal.sLower,
          //   x2: newVal.sUpper,
          //   fillColor: '#B3F7CA',
          //   opacity: 0.4,
          //   label: {
          //     borderColor: '#B3F7CA',
          //     style: {
          //       fontSize: '10px',
          //       color: '#fff',
          //       background: '#00E396',
          //     },
          //     //offsetY: -10,
          //     text: 'Confidence interval',
          //   }
          // }
          ]
        }};
        this.options = o;
      } else {
        this.options = this.optionsOrig;
      }
      this.series = newVal.series;
    },
  },
  methods: {
  }
}
</script>